.footerWrapper {
  background-color: #333;
  color: #fff;
  direction: rtl;
}
.footerContainer{
  width: 1140px;
  margin: 0 auto;
  padding: 40px 20px;
}

.footerContainer .footerCopyRight{
  text-align: center;
  color: #999;
  font-size: 12px;
  padding-top: 30px;
}

@media screen and (max-width: 1199px){
  .footerContainer {
    width: 100%;
    margin: 0;
  }
}